.popup {
    border-radius: 10vh;
    display: block;
    position: fixed;
    z-index: 100000000000;
    right: 1.5%;
    top: 1.5%;
    width: 96%;
    height: 96%;
    background-color: rgba(255, 255, 255, 0.5);
}

.helpscreen {
    position: absolute;
    top: 5%;
    right: 12.5%;
    width: 75%;
    height: 90%;
}

.leftArrow {
    width: 8%;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.rightArrow {
    width: 8%;
    z-index: 1;
    position: absolute;
    top: 50%;
    right: -2%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.active,
.dot:hover {
    background-color: #717171;
}
.closePopup {
    cursor: pointer;
    position: absolute;
    right: 13%;
    top: 15%;
    z-index: 5;
    transform: translate(-50%, -50%);
    height: 10%;
}

.text {
    position: absolute;
    z-index: 6;
    background-color: white;
    width: 15%;
    border-radius: 15px;
    word-wrap: break-word;
    padding: 5px 5px;
    font: 'Halcyon Regular';
    font-family: 'Halcyon Regular';
    transform: translate(-50%, -50%);
}
