.dropdown {
  overflow: hidden;
  /* margin-right: 1%; */
}

.dropdown .dropbtn {
  font-size: 1.5rem;
  border: none;
  outline: none;
  color: white;
  background-color: inherit;
  /* font-family: inherit; */
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
  font-size: 1.5rem;
  margin: 0;
  cursor: pointer;
  padding: 1rem;
  width: 100%;
  text-align: left;
}
.sample {
  cursor: pointer;
  width: 6.5vh !important;
  height: 6.5vh;
  padding: 0.7rem !important;
  border: 2px solid rgb(77, 75, 75) !important;
  background-color: white !important;
  border-radius: 50%;
}
.sample img {
  width: 100%;
}
.margin {
  margin-left: 20px !important;
}
.arrow {
  /* content: ""; */
  width: 0px;
  height: 0px;
  /* background-color: #58b7d7; */
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 20px solid #58b7d7;
  /* display: inline-block; */
  position: fixed;
  z-index: 10;
  top: 11.2%;
  right: 25%;
  /* left: 30px; */
  /* transform: translateY(-50%) rotate(45deg); */
}
.dropdownContent {
  display: none;
  position: relative;
  background-color: #58b7d7;
  min-width: 220px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
  z-index: 1;
}
.dropdownContent.show {
  display: block;
}
.dropdownContent a {
  color: white;
  padding: 0.8rem 1.5rem 0.8rem 3rem;
  margin: 0 25px;
  border-radius: 5px;
  text-decoration: none;
  display: block;
  text-align: left;
  transition: color 0.1s ease-out;
  box-shadow: none;
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
}
.dropdownContent a:hover {
  color: black;
  transition: color 0.3s ease-in-out;
}
.dropdownContent.outside {
  z-index: 1000;
  position: fixed;
  top: 13%;
  right: 17.5%;
  border-radius: 10px;
  max-height: 50vh;
  /* overflow-y: scroll; */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
  /* overflow-x: visible; */
}
.outside {
  /* overflow-y: scroll; */
}
.flexClass {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.droDown_inner {
  display: none;
  border-radius: 10px;
}

.droDown_inner::-webkit-scrollbar {
  display: none;
}
.selected {
  background-color: #f0f7fa;
  color: black !important;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.sidebar {
  border: 2px solid red;
  position: relative;
  left: 2vh;
}
.scroll-indicator {
  color: #f0f7fa;
  position: fixed;
  z-index: 10;
  top: 55%;
}

.console-container {
  position: relative;
  top: -3.8vh;
}

/* for consloe text div style */
.response {
  width: 100%;
  /* height: 100%; */
  height: calc(100% - 1.3rem);
  top: 3vh;
  overflow-y: auto;
  text-decoration: none;
  color: white;
  font-size: 1.1rem;
  font: "Halcyon Regular";
  background-color: transparent;
  border: none;
  font-family: "Source Code Pro", monospace;
  padding-bottom: 2rem;
  outline: none;
  padding-bottom: 100px;
  border: "1px solid rgb(78 76 76 / 91%)";
}
.console-button-container p {
  cursor: pointer;
  padding: 0.2rem;
}
.console-button-container p:hover {
  background-color: rgb(78 76 76 / 91%);
  border-radius: 3px;
}

.response::-webkit-scrollbar {
  width: 10px;
}
.response::-webkit-scrollbar-track {
  background-color: black;
  border: 1px solid #808080;
  border-radius: 6px;
}

.response::-webkit-scrollbar-thumb {
  background-color: #606060;
  border-radius: 6px;
}

.cm-editor.cm-focused {
  border: none;
  outline: none;
}

.cm-scroller::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.cm-scroller::-webkit-scrollbar-track {
  background-color: none;
  border: 1px solid #808080;
  border-radius: 6px;
}

.cm-scroller::-webkit-scrollbar-thumb {
  background-color: #606060;
  border-radius: 6px;
}
@media only screen and (max-width: 1024px) {
  html {
    font-size: 10px;
  }
}
