.container {
    position: absolute;
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Halcyon_SemiBold' !important;

    background: rgba(255, 255, 255, 0.7);
    z-index: 95000;

    /* background-color: red; */
}

.box {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 400px;
    min-height: 150px;
    padding: 1rem 2rem;

    background: linear-gradient(to right, #f0eded, white);
    border-radius: 4rem;
    border: 1px solid white;
    box-shadow: 0.5rem 0.5rem 2rem #d9d5d4;
}

.btngroup {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.closeBtn {
    position: relative;
    width: 3.5rem;
    left: 100%;
    top: -40px;
    /* overflow: hidden; */
    cursor: pointer;
}

.btn {
    min-width: 2rem;
    padding: 0.65rem 2rem;
    border: 0px;
    border-radius: 1.8rem;
    height: 40px;

    color: white;
    background: linear-gradient(to right, #fda84e, #f97d62);
    cursor: pointer;
}

.btn:hover {
    box-shadow: 0 0 0.5rem #aaa9a8;
}

.text {
    /* margin: 0 2rem; */
    margin-top: -2rem;
    margin-bottom: 1rem;
    text-align: center;
    font-family: 'Halcyon_SemiBold' !important;
}

.selectionContainer {
    position: absolute;
    width: 100vw;
    height: 100vh;
    font-family: 'Halcyon_SemiBold' !important;
    background: rgba(255, 255, 255, 0.7);
    z-index: 95000;

    display: flex;
    justify-content: center;
    align-items: center;
}

.selectionbox {
    width: 38vh;
    height: 35vh;
    padding: 1rem 1rem;
    background: linear-gradient(to right, #f0eded, white);
    border-radius: 7vh;
    border: 1px solid white;
    box-shadow: 0.5rem 0.5rem 2rem #d9d5d4;
    position: relative;
}
.selection_SubBox {
    width: 35vh;
    height: 31vh;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 15% 40% 15% 30%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.img {
    width: 18vh;
    margin: -5% 24% 0 24.5%;
}
.closeBtns {
    position: absolute;
    top: -5%;
    right: -6%;
    width: 8vh;
}
.btns {
    padding: 1.5vh 3vh;
    border: 0px;
    border-radius: 1.8rem;
    color: white;
    background: linear-gradient(to right, #fda84e, #f97d62);
    cursor: pointer;
    font-family: 'Halcyon_Medium' !important;
    font-size: 1.5vh !important;
}
