.error {
  position: absolute;
  right: 4vw;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.error .hoverImg {
  display: none;
  width: 4vw;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 58%;
  left: 50%;
  z-index: 99;
}
.error:hover .hoverImg {
  display: block;
}
.upload {
  cursor: pointer;
