.erasePopupMain {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Halcyon_SemiBold";
  background: hsla(0, 0%, 100%, 0.7);
  z-index: 95000;
}
.erasePopupBox {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 400px;
  min-height: 150px;
  padding: 1rem 2rem;
  background: linear-gradient(90deg, #f0eded, #fff);
  border-radius: 4rem;
  border: 1px solid #fff;
  box-shadow: 0.5rem 0.5rem 2rem #d9d5d4;
}
.erasePopupText {
  margin-bottom: 1rem;
  text-align: center;
  font-family: "Halcyon_SemiBold";
  position: relative;
  top: 0.5rem;
  white-space: pre-line;
}
.btnGroup {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.erasePopupBtn {
  min-width: 2rem;
  padding: 0.65rem 2rem;
  border: 0;
  border-radius: 1.8rem;
  height: 40px;
  color: #fff;
  background: linear-gradient(90deg, #fda84e, #f97d62);
  cursor: pointer;
  font-family: "Halcyon_SemiBold";
}
