.pupUpBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60vw;
  height: 70vh;
  padding: 0rem;
  background-color: #f0eded;
  border-radius: 2rem;
  border: 1px solid white;
  box-shadow: 0.5rem 0.5rem 2rem #d9d5d4;
  font-family: "Halcyon Regular";
}
.pupUpCloseButton {
  width: 3rem;
  cursor: pointer;
}
.popPup-container {
  width: 100%;
  height: 100%;
  padding: 0rem 1rem 1rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-color: aqua; */
}
.popPupCodeName-container {
  width: 25%;
  height: 60vh;
  background-color: white;
  border-radius: 1rem;
  padding: 2%;
  overflow: auto;
}
.popPupCode-container {
  width: 74%;
  height: 60vh;
  background-color: white;
  border-radius: 1rem;
}
.popPupCodeName-container p {
  /* width: 25%; */
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 0.5rem;
  border-left: 1px rgb(101, 89, 89) solid;
  border-right: 1px rgb(101, 89, 89) solid;
  border-bottom: 1px rgb(101, 89, 89) solid;
  cursor: pointer;
}
/* .popPupCodeName-container p:first-of-type,
.popPupCodeName-container p:nth-child(8),
.popPupCodeName-container p:nth-child(14) {
  border-top: 1px rgb(101, 89, 89) solid;
} */
.popPupCodeName-container p:hover {
  background-color: #9addf9;
  color: black;
}
.select_sampleCode {
  background-color: #56b7d7;
  color: white;
}
.exportButton {
  position: fixed;
  right: 22%;
  bottom: 20vh;
  cursor: pointer;
}
.errorFileName {
  position: fixed;
  right: 22%;
  bottom: 24vh;
  color: red;
}

.popPupCodeName-container::-webkit-scrollbar {
  width: 10px;
}
.popPupCodeName-container::-webkit-scrollbar-track {
  /* background-color: black; */
  border: 1px solid #808080;
  border-radius: 6px;
}

.popPupCodeName-container::-webkit-scrollbar-thumb {
  background-color: #606060;
  border-radius: 6px;
}
.exportToolTip {
  position: fixed;
  right: 22%;
  bottom: 26vh;
  color: white;
  background-color: #56b7d7;
  padding: 5px;
  border-radius: 10px;
  font-size: 0.8rem;
}
