/* header swape button style */
.swipe-container {
  position: relative;
  display: inline-block;
  width: 200px;
  height: 40px;
  border: 1px solid #ccc;
  overflow: hidden;
  border-radius: 20px;
  cursor: pointer;
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
}

.swipe-handle {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background-color: #56b7d7;
  border-radius: 20px;
  transition: transform 0.3s ease-in-out;
}

.swipe-container.true .swipe-handle {
  transform: translateX(100%);
}

.left-text {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: white;
}
.swipe-container.true .left-text {
  color: #000;
}

.right-text {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #000;
}
.swipe-container.true .right-text {
  color: white;
}

