.sub-header-container {
  display: flex;
  justify-content: left;
  align-items: center;
  position: fixed;
  left: 15%;
  top: 10.5%;
  height: 7vh;
  overflow: auto;
  z-index: 1000;
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
}
.sub-header-file-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* width: 10%; */
  min-width: 7rem;
  max-width: 10rem;
  height: 100%;
  padding: 0 1rem 0 1rem;
