}

.inputFilePopupContainer_inner {
  position: relative;
  background: linear-gradient(90deg, #f0eded, #fff);
  /* background-color: #56b7d7; */
  border: 1px solid #fff;
  border-radius: 4rem;
  box-shadow: 0.5rem 0.5rem 2rem #d9d5d4;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 150px;
  padding: 1rem 2rem;
  width: 400px;
  height: 200px;
}
.file_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.inputFilePopupContainer_closeBtn {
  width: 45px;
  position: absolute;
  top: 5px;
  left: 346px;
}

.sameFile_exists_ans {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.sameFile_exists_ans p {
  text-align: center;
  background: linear-gradient(
    to bottom right,
    rgb(244, 166, 81),
    rgb(242, 123, 97)
  );
