.create-file-container {
  width: 15%;
  height: 86vh;
  background-color: #eeeeee;
  /* border-radius: 20px; */
  border: 1px solid rgba(59, 59, 59, 0.5);
  font: "Halcyon Regular";
  font-family: "Halcyon Regular";
}

.program-name {
  position: fixed;
  left: 0;
  top: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  /* height: 13%; */
  height: 10.4vh;
  width: 15%;
  border-bottom: 1px solid rgba(59, 59, 59, 0.5);
  cursor: pointer;
}

.files-container {
  position: fixed;
  left: 0;
  top: 20.4vh;
  z-index: 10000;
  /* background-color: aqua; */
  overflow: auto;
  height: 60vh;
  width: 15%;
  border-right: 1px solid rgba(59, 59, 59, 0.5);
  /* border-right: ; */
  /* word-wrap: break-word; */
}
/* .files-container::-webkit-scrollbar {
    display: none; 
  
} */
.files-container::-webkit-scrollbar {
  width: 10px;
}
.files-container::-webkit-scrollbar-track {
  /* background-color: black; */
  border: 1px solid #808080;
  border-radius: 6px;
}

.files-container::-webkit-scrollbar-thumb {
  background-color: #808080;
  border-radius: 6px;
}
.each-file {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid rgba(59, 59, 59, 0.5);
  width: 99.8%;
  cursor: pointer;
  /* word-wrap: break-word; */
  /* background-color: aqua; */
}
.each-file:hover {
  background-color: #f0f7fa;
}

.each-file p {
  max-width: 90%;
  overflow: hidden;
}
.input_fileName {
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  /* padding: 1rem; */
  border-bottom: 1px solid rgba(59, 59, 59, 0.5);
  width: 100%;
}
.input_text_class {
  width: 100%;
  height: 7vh;
  border: none;
  outline: none;
  padding: 1rem;
}
.input_error_class {
  width: 100%;
  height: 7vh;
  border: none;
  outline: none;
  padding: 1rem;
  text-decoration: underline;
  text-decoration-color: red;
  text-decoration-style: solid;
}

.each-file:has(.cross_button:hover) {
  background-color: rgb(204, 141, 141) !important;
}
.newFilenameError {
  position: fixed;
  left: 1%;
  /* z-index: 10000000; */
  /* background-color: rgb(255, 255, 255, 0); */
  color: red;
}
.fileMenuDiv {
  position: relative;
  /* z-index: 1; */
}
.fileMenu_icons {
  background-color: #8acdea;
  z-index: 100000001;
  font-size: 16px;
  color: white;
  position: absolute;
  /* top: -14px;
  left: -66px;
  padding: 5px; */
  top: 107%;
  /* left: -58%; */
  left: -212%;
  padding: 5px 50px 5px 5px;
  border-radius: 5px;
  display: none;

  /* width: 8vw; */
}
.fileMenu_icons_show {
  display: block;
}
.fileMenu_icons_inner {
  position: relative;
  height: 100%;
  width: 100%;
  /* border: 1px solid red; */
}
.menu_arrow {
  background-color: #8acdea;
  /* background-color: #eab48a; */
  height: 35%;
  width: 35%;
  position: absolute;
  top: -16%;
  left: 117%;
  transform: rotate(45deg);
  z-index: -1;
}
.each_file_menuP {
  max-width: 100% !important;
  cursor: pointer;
  border-radius: 5px;
  padding: 3px 0px;
}
.each_file_menuP:hover {
  background-color: #808080;
}
.mainFile_meuDiv {
  padding: 5px 73px 5px 5px;
}
.mainFile_menu_arrow {
  top: -44%;
  left: 195%;
  height: 72%;
  width: 56%;
}
.fileMenu_toolTip {
  position: absolute;
  background-color: #56b7d7;
  color: #eeeeee;
  padding: 5px;
  border-radius: 10px;
  font-size: 0.8rem;
  text-align: center;
  visibility: hidden;
  top: 111%;
}
.cross_button:hover ~ .fileMenu_toolTip {
  visibility: visible;
}
